// @flow

import moment from 'moment'
import type Moment from 'moment'

import momentLocale from './moment.cn'
import { isNotNilOrEmpty } from './ramda-extra'

const FORMAT_DATETIME_MEDIUM = 'YYYY-MM-DD HH:mm'
const LOCALE = 'zh-cn'

const setupLocale = (): void => {
  // $FlowFixMe
  if (moment.locales().indexOf(LOCALE) !== -1) {
    moment.updateLocale(LOCALE) // clear existing locale
  }

  moment.locale('zh-cn', momentLocale)
  moment.locale('en') // reset active locale
}

const formatTime = (
  time?: string | Date | Moment,
  format: string = FORMAT_DATETIME_MEDIUM
): string =>
  // eslint-disable-next-line prettier/prettier
  isNotNilOrEmpty(time) ? moment(time).locale('zh-cn').format(format) : ''

export { FORMAT_DATETIME_MEDIUM, formatTime, setupLocale }
